body {
    background-color: #65A7FB;
}

.row {
    margin: 0;
    max-width: 15.5rem;
}

.num {
    margin: 0.04rem;
    padding: 0;
    border: 1px;
    background-color: black;
    color: white;
    height: 4rem;
    width: 5rem;
    font-family: 'Gemunu Libre', sans-serif;
}

.showup {
    height: 2rem;
    max-width: 14rem;
    display: block;
    margin: auto;
    margin-left: 1.2rem;
    margin-right: 1.2rem;
    margin-bottom: .5rem;
    padding-top: .5rem;
    padding-bottom: 2rem;
    font-size: 1.3rem;
    text-align: left;
    font-family: 'Gemunu Libre', sans-serif;
}

.show {
    height: 4rem;
    max-width: 14rem;
    display: block;
    margin: auto;
    margin-left: 1.2rem;
    margin-right: 1.2rem;
    margin-bottom: 1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 3rem;
    text-align: left;
    font-family: 'Gemunu Libre', sans-serif;
}

.hs {
    margin: auto;
    margin-top: 3.5rem;
    margin-bottom: 2rem;
    padding: 3.19px;
    padding-top: 1.5rem;
    text-align: center;
    font-family: 'Gemunu Libre', sans-serif;
    font-size: 35px;
    letter-spacing: 0;
    border: .2rem solid black;
    width: 22rem;
    max-width: 22rem;
    height: 9rem;
    background-color: white;
}

.superd {
    margin: auto;
    margin-top: 6rem;
    background-color: white;
    width: 15.25rem;
    max-width: 15.25rem;
    height: max-content;
    padding-bottom: .1rem;
    box-shadow: 0px 0px 10px 1px black;
    border-radius: 2%;
}

.extra1 {
    border-bottom-left-radius: .35rem;
}

.extra2 {
    border-bottom-right-radius: .35rem;
}

button {
    transition-duration: .1s;
}

button:hover {
    background-color: #0e0e0ef1;
}

h1 {
    font-size: calc(1.375rem + 1vw);
}

.material-icons-outlined {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    margin: auto;
    margin-top: .5rem;
  
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
}